<template>
    <div>
        <el-table
                :data="tableData"
                border
                style="width: 100%"
                row-key="id"
                :tree-props="{children: 'cities', hasChildren: 'hasChildren'}">
            <el-table-column
                    prop="name"
                    label="地区">
            </el-table-column>
            <el-table-column
                    prop="confirmedCount"
                    label="确诊">
            </el-table-column>
            <el-table-column
                    prop="deadCount"
                    label="死亡">
            </el-table-column>
            <el-table-column
                    prop="curedCount"
                    label="治愈">
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="100">
                <template slot-scope="scope" v-if="scope.row.cities">
                    <el-button @click="showGrow(scope.row)" type="primary" size="small">增长图</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog
                title="提示"
                :visible.sync="showGrowChart"
                width="70%">
            <v-chart :options="chartOption"></v-chart>
        </el-dialog>
    </div>
</template>

<script>
  /* eslint-disable no-console,no-unused-vars */

  import {mapState, mapMutations} from 'Vuex'
  import axios from 'axios'
  import ECharts from 'vue-echarts'
  import echarts from 'echarts'

  export default {
    name: "ProvinceList",
    components: {
      "v-chart": ECharts
    },
    data() {
      return {
        tableData: [],
        showGrowChart: false,
        chartOption: {
          color: ['#3398DB'],
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: [],
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [
            {
              name: '确诊',
              type: 'bar',
              barWidth: '60%',
              data: []
            }
          ]
        }
      }
    },
    computed: {
      ...mapState(['provinceData', 'timeLineData'])
    },
    methods: {
      ...mapMutations(['setTimeLineData', 'setProvinceData']),
      showGrow: function({provinceShortName}) {
        const titleReg = /新增(\d+)例/
        console.log(this.timeLineData.length)
        const provinceTimeLine = this.timeLineData.filter(timeLine =>
          timeLine.title.indexOf(provinceShortName) > -1 && timeLine.title.search(titleReg) > -1)
          .map(timeLine => ({
            count: parseInt(timeLine.title.match(titleReg)[1]),
            date: this.dateFormat(timeLine.pubDate, 'yyyy/MM/dd')
          })).reverse()
        this.chartOption.xAxis[0].data = provinceTimeLine.map(timeLine => timeLine.date)
        this.chartOption.series[0].data = provinceTimeLine.map(timeLine => timeLine.count)
        this.showGrowChart = true
      },
      dateFormat: (date, fmt) => {
        if (!date) return ''
        if (typeof date === 'number') date = new Date(date)
        let attributes = {
          "M+": date.getMonth() + 1, //月份
          "d+": date.getDate(), //日
          "h+": date.getHours(), //小时
          "m+": date.getMinutes(), //分
          "s+": date.getSeconds(), //秒
          "S": date.getMilliseconds() //毫秒
        }
        if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
        for (let attr in attributes)
          if (new RegExp(`(${attr})`).test(fmt)) {
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (attributes[attr]) : (("00" + attributes[attr]).substr(("" + attributes[attr]).length)))
          }
        return fmt
      },
    },
    async mounted() {
      const {data: provinceData} = await axios.get('./json/area.json')
      const {data: timeLineData} = await axios.get('./json/timeline.json')
      this.setProvinceData(provinceData)
      this.setTimeLineData(timeLineData)
      let counter = 1
      this.tableData = provinceData.map(province => ({
        ...province,
        id: counter++,
        name: province.provinceShortName,
        cities: province.cities.map(city => ({
          ...city,
          id: counter++,
          name: city.cityName
        }))
      }))
    }
  }
</script>

