import Vue from 'vue'
import Router from 'vue-router'
import provinceList from '@/components/ProvinceList'

Vue.use(Router)

export default new Router({
    routes: [
        { path: '/', redirect: 'home' },
        { path: '/list', name: 'list', component: provinceList },
        { path: '/detail', name: 'detail', component: provinceList }
    ]
})
